      </h3>
      <p>()()</p>
      <demo>
        <template slot="source">
          <vm-button type="primary" icon="edit"></vm-button>
          <vm-button type="primary" icon="heart"></vm-button>
          <vm-button type="primary" icon="share"></vm-button>
          <vm-button type="primary" icon="search"></vm-button>
          <vm-button type="primary">
            <vm-icon type="upload"></vm-icon>
          </vm-button>
        </template>
        <template slot="description">
           <code>icon</code> <code>icon</code>  <a href="https://vue-multiple.github.io/icon/"
                                                                 target="_blank"></a>  <code>icon</code> 
          <code>icon</code>  <code>i</code> 
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeD"><code class="html javascript"></code></pre>
        </template>
