          <vm-button type="primary" size="default"></vm-button>
          <vm-button type="primary" size="small"></vm-button>
          <vm-button type="primary" size="mini"></vm-button>
        </template>
        <template slot="description">
          <code>large</code><code>small</code><code>mini</code> <code>size</code>
        </template>
        <template slot="highlight">
          <pre v-highlightjs="sourcecodeG"><code class="html javascript"></code></pre>
        </template>
      </demo>
    </div>
    <div class="button-demo__item">
      <h3 id="attributes">
        <a href="#attributes" aria-hidden="true" class="header-anchor"></a>
        Button Attributes
      </h3>
      <table class="table">
        <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td>size</td>
          <td></td>
          <td>string</td>
          <td>large,default,small,mini</td>
          <td></td>
        </tr>
        <tr>
          <td>type</td>
          <td></td>
          <td>string</td>
          <td>primary,success,warning,danger,info,text,default</td>
          <td></td>
        </tr>
        <tr>
          <td>plain</td>
          <td></td>
          <td>Boolean</td>
          <td></td>
          <td>false</td>
        </tr>
        <tr>
          <td>loading</td>
          <td></td>
          <td>Boolean</td>
          <td></td>
          <td>false</td>
        </tr>
        <tr>
          <td>loadingIcon<i class="new"></i></td>
          <td></td>
          <td>string</td>
          <td>load-a,load-b,load-c,load-d</td>
          <td>load-a</td>
        </tr>
        <tr>
          <td>disabled</td>
          <td></td>
          <td>boolean</td>
          <td></td>
          <td>false</td>
        </tr>
        <tr>
          <td>icon</td>
          <td></td>
          <td>string</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>autofocus</td>
          <td></td>
          <td>boolean</td>
          <td></td>
          <td>false</td>
        </tr>
        <tr>
          <td>native-type</td>
          <td> type </td>
          <td>string</td>
          <td>button,submit,reset</td>
          <td>button</td>
        </tr>
